/**
 * CSS Custom Properties for Laerdal Life Design Tokens
 * 
 * This file generates CSS custom properties (CSS variables) from design tokens
 * for use in CSS files and styled-components
 */

:root {
  /* Primary Colors */
  --color-primary-50: #e6f4fa;
  --color-primary-100: #b3d9ed;
  --color-primary-200: #80bfdf;
  --color-primary-300: #4da4d2;
  --color-primary-400: #2E7FA1;
  --color-primary-500: #25617a;
  --color-primary-600: #1c4a5c;
  --color-primary-700: #13333e;
  --color-primary-800: #0a1c20;
  --color-primary-900: #020607;

  /* Gray Colors */
  --color-gray-50: #f9fafb;
  --color-gray-100: #f3f4f6;
  --color-gray-200: #e5e7eb;
  --color-gray-300: #d1d5db;
  --color-gray-400: #9ca3af;
  --color-gray-500: #6b7280;
  --color-gray-600: #4b5563;
  --color-gray-700: #374151;
  --color-gray-800: #1f2937;
  --color-gray-900: #111827;

  /* Brand Colors */
  --color-brand-blue: #2E7FA1;
  --color-brand-blue-light: #e6f4fa;
  --color-brand-blue-dark: #25617a;

  /* Semantic Colors */
  --color-success-50: #f0fdf4;
  --color-success-500: #22c55e;
  --color-success-600: #16a34a;

  --color-warning-50: #fffbeb;
  --color-warning-500: #f59e0b;
  --color-warning-600: #d97706;

  --color-error-50: #fef2f2;
  --color-error-500: #ef4444;
  --color-error-600: #dc2626;

  /* Typography */
  --font-family-sans: 'Lato', Arial, Helvetica, sans-serif;
  --font-family-mono: ui-monospace, SFMono-Regular, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;

  --font-size-xs: 0.75rem;
  --font-size-sm: 0.875rem;
  --font-size-base: 1rem;
  --font-size-lg: 1.125rem;
  --font-size-xl: 1.25rem;
  --font-size-2xl: 1.5rem;
  --font-size-3xl: 1.875rem;
  --font-size-4xl: 2.25rem;
  --font-size-5xl: 3rem;

  --font-weight-normal: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;

  /* Spacing */
  --spacing-1: 0.25rem;
  --spacing-2: 0.5rem;
  --spacing-3: 0.75rem;
  --spacing-4: 1rem;
  --spacing-5: 1.25rem;
  --spacing-6: 1.5rem;
  --spacing-8: 2rem;
  --spacing-10: 2.5rem;
  --spacing-12: 3rem;
  --spacing-16: 4rem;
  --spacing-20: 5rem;
  --spacing-24: 6rem;
  --spacing-32: 8rem;

  /* Border Radius */
  --radius-sm: 0.125rem;
  --radius-default: 0.25rem;
  --radius-md: 0.375rem;
  --radius-lg: 0.5rem;
  --radius-xl: 0.75rem;
  --radius-2xl: 1rem;
  --radius-full: 9999px;

  /* Shadows */
  --shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
  --shadow-default: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);
  --shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  --shadow-xl: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);

  /* Component-specific tokens */
  --button-primary-bg: var(--color-brand-blue);
  --button-primary-bg-hover: var(--color-brand-blue-dark);
  --button-primary-text: white;
  --button-primary-border: var(--color-brand-blue);

  --button-secondary-bg: white;
  --button-secondary-bg-hover: var(--color-primary-50);
  --button-secondary-text: var(--color-brand-blue);
  --button-secondary-border: var(--color-brand-blue);

  --input-bg: white;
  --input-border: var(--color-gray-300);
  --input-border-focus: var(--color-brand-blue);
  --input-text: var(--color-gray-900);
  --input-placeholder: var(--color-gray-400);

  --card-bg: white;
  --card-border: var(--color-gray-200);
  --card-shadow: var(--shadow-default);
}

/* Dark mode support (optional - add if needed) */
@media (prefers-color-scheme: dark) {
  :root {
    --color-gray-50: #111827;
    --color-gray-100: #1f2937;
    --color-gray-200: #374151;
    --color-gray-300: #4b5563;
    --color-gray-400: #6b7280;
    --color-gray-500: #9ca3af;
    --color-gray-600: #d1d5db;
    --color-gray-700: #e5e7eb;
    --color-gray-800: #f3f4f6;
    --color-gray-900: #f9fafb;

    --card-bg: var(--color-gray-800);
    --input-bg: var(--color-gray-800);
  }
}

/* Utility classes for design tokens */
.text-brand-blue { color: var(--color-brand-blue); }
.bg-brand-blue { background-color: var(--color-brand-blue); }
.border-brand-blue { border-color: var(--color-brand-blue); }

.text-primary-400 { color: var(--color-primary-400); }
.bg-primary-400 { background-color: var(--color-primary-400); }
.border-primary-400 { border-color: var(--color-primary-400); }

.bg-primary-50 { background-color: var(--color-primary-50); }
.bg-primary-100 { background-color: var(--color-primary-100); }

.text-primary-600 { color: var(--color-primary-600); }

.bg-brand-darkBlue { background-color: var(--color-brand-blue-dark); }
.hover\:bg-brand-darkBlue:hover { background-color: var(--color-brand-blue-dark); }

.focus\:ring-primary-400:focus {
  --tw-ring-color: var(--color-primary-400);
  box-shadow: var(--tw-ring-inset) 0 0 0 calc(2px + var(--tw-ring-offset-width)) var(--tw-ring-color);
}

/* Component-specific utility classes */
.btn-primary {
  background-color: var(--button-primary-bg);
  color: var(--button-primary-text);
  border-color: var(--button-primary-border);
}

.btn-primary:hover {
  background-color: var(--button-primary-bg-hover);
}

.btn-secondary {
  background-color: var(--button-secondary-bg);
  color: var(--button-secondary-text);
  border-color: var(--button-secondary-border);
}

.btn-secondary:hover {
  background-color: var(--button-secondary-bg-hover);
}

.input-default {
  background-color: var(--input-bg);
  border-color: var(--input-border);
  color: var(--input-text);
}

.input-default:focus {
  border-color: var(--input-border-focus);
}

.input-default::placeholder {
  color: var(--input-placeholder);
}

.card-default {
  background-color: var(--card-bg);
  border-color: var(--card-border);
  box-shadow: var(--card-shadow);
}
