.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.heading{
  background: #097997;
    color: #fff;
    padding: 15px 1.5rem;
    font-size: 22px;
    margin-bottom: 0;
}
.btn-menu {
    width: 120px;
}
.btn-menu svg{
  vertical-align: middle;
    display: inline-block;
}

hr {
    margin: 5px 0;
    border-top-width: 2px;
    border-color: #b5b1b1;
}